<template>
    <div>
    <p class="question">{{question}}</p>
    <div class="answer" @click="$emit('answered','A')">{{answerA}}</div>
    <div class="answer" @click="$emit('answered','B')" >{{answerB}}</div>
    </div>
</template>

<script>
export default {
  name: "Question",
  props: {
    question: String,
    answerA: String,
    answerB: String
  }
};
</script>

